export class Scene {
    constructor(engine) {
        this._engine = engine;
        this.children = [];
    }
    add(object) {
        this.children.push(object);
    }
    remove(object) {
        const index = this.children.indexOf(object);
        if (index !== -1) {
            this.children.splice(index, 1);
        }
    }
}
