export function addProxy(proxyObj, setCb, getCb) {
    const handler = {
        get(target, key) {
            let result = target[key];
            getCb && getCb(result);
            return result;
        },
        set(target, key, value) {
            target[key] = value;
            setCb && setCb(this);
            return this;
        },
    };
    return new Proxy(proxyObj, handler);
}
export function cloneUniforms(src) {
    const dst = {};
    for (const u in src) {
        dst[u] = {};
        for (const p in src[u]) {
            const property = src[u][p];
            if (property &&
                (property.isColor || property.isMatrix3 || property.isMatrix4 || property.isVector2 || property.isVector3 || property.isVector4 || property.isTexture || property.isQuaternion)) {
                dst[u][p] = property.clone();
            }
            else if (Array.isArray(property)) {
                dst[u][p] = property.slice();
            }
            else {
                dst[u][p] = property;
            }
        }
    }
    return dst;
}
